@import url(https://fonts.googleapis.com/css2?family=Abril+Fatface);
/* 'https://fonts.googleapis.com/css2?family=Abril+Fatface&family=Rakkas&display=swap' */

html {
	background: url(/static/media/bg.d43561be.JPG);
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center;
	height: 100vh;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}
body {
	padding: 0;
	margin: 0;
	box-sizing: border-box;

	/* font-family: 'Rakkas', cursive; */
	font-family: 'Abril Fatface', cursive;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.App {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	max-width: 100vw;
}

#header {
	display: flex;
	justify-content: space-around;
	height: 15vh;
	align-items: center;
	color: rgb(233, 72, 32);
	text-shadow: 0px 0px 5px #000000;
}

h1 {
	font-size: 5rem;
	font-style: italic;
	border: 4px solid #ffffff;
	box-shadow: 20px 38px 34px -26px hsla(0, 0%, 0%, 0.2);
	border-radius: 255px 15px 225px 15px/15px 225px 15px 255px;
	background-color: rgba(0, 0, 0, 0.7);
	padding: 10px;
}

main {
	display: flex;
	align-items: center;
	justify-content: center;
	height: 70vh;
	width: 100%;

	max-width: 700px;
}

#footer {
	display: flex;
	justify-content: space-around;
	align-items: center;
	width: 80%;
	height: 15vh;
}

#resetButton {
	font-size: 2.2rem;
	color: white;
	font-style: italic;
	border: 4px solid #ffffff;
	box-shadow: 20px 38px 34px -26px hsla(0, 0%, 0%, 0.2);
	border-radius: 255px 15px 225px 15px/15px 225px 15px 255px;
	background-color: rgba(0, 0, 0, 0.7);
	padding: 10px;
}

#resetButton:hover {
	background-color: #e94820;
	color: black;
	transition: all 130ms ease-in;
	border-color: black;
}

#winCounter {
	font-size: 2.2rem;
	color: white;
	font-style: italic;
	border: 4px solid #ffffff;
	box-shadow: 20px 38px 34px -26px hsla(0, 0%, 0%, 0.2);
	border-radius: 255px 15px 225px 15px/15px 225px 15px 255px;
	background-color: rgba(0, 0, 0, 0.7);
	padding: 30px;
}

@media only screen and (max-width: 767px) {
	h1 {
		font-size: 2.5rem;
	}

	#winCounter {
		font-size: 1.6rem;
		padding: 15px;
	}

	#resetButton {
		font-size: 1rem;
	}
}

.square {
	position: relative;
}
.card {
	box-sizing: border-box;
	padding: 10px;

	position: absolute;
	height: 100%;
	width: 100%;
	display: flex;
	justify-content: center;
	text-align: center;
	align-items: center;
	border: 2px solid black;
	box-shadow: 20px 38px 34px -26px hsla(0, 0%, 0%, 0.2);
	border-radius: 255px 15px 225px 15px/15px 225px 15px 255px;
	background-color: rgba(255, 255, 255, 0.95);
	transition: all 130ms ease-in;
}

.card:hover {
	-webkit-transform: rotate(2deg);
	        transform: rotate(2deg);
}

.match {
	color: black;
	background-color: rgba(0, 0, 0, 0.7);
}

.card--text {
	pointer-events: none;
	z-index: 3;
}

.cross {
	position: absolute;
	z-index: 4;
	width: 100%;
	pointer-events: none;
}

.joker .cross {
	-webkit-transform: translate(0, 10px);
	        transform: translate(0, 10px);
}
@media only screen and (max-width: 992px) {
	.card {
		padding: 2px;
	}
	.card--text {
		font-size: 0.6rem;
	}
}

@media only screen and (max-height: 850px) {
	.card {
		padding: 2px;
	}
	.card--text {
		font-size: 0.6rem;
	}
}

.game {
	min-width: 350px;
	min-height: 350px;
	max-height: 100%;

	display: grid;

	grid-template-columns: repeat(5, minmax(50px, 12vh));
	grid-template-rows: repeat(5, minmax(50px, 12vh));
	grid-gap: 10px;
	gap: 10px;
}

